import { useCallback, useState } from "react";
import { notification } from "antd";

export function handleError(err) {
  if (err?.message) {
    notification.error({
      message: err?.message,
      placement: "top",
    });
  } else {
    notification.error({
      message: "Something went wrong",
      placement: "top",
    });
  }
}
const useHttp = () => {
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState(null);

  //hm sendRequest vi 2 params, requestConfig l 1 mng cha thng tin url, method, headers, body; applyData l hm x l data tr v t fetch
  const sendRequest = useCallback(async (requestConfig, applyData) => {
    //t trng thi ang fetch l true, error hin ti l null
    setIsLoading(true);
    setError(null);
    try {
      ///fetch vi cc config c truyn vo,
      const res = await fetch(requestConfig.url, {
        method: requestConfig.method ? requestConfig : "GET",
        headers: requestConfig.headers ? requestConfig.headers : {},
        body: requestConfig.body ? JSON.stringify(requestConfig.body) : null,
      });
      const data = await res.json();
      //gi hm applyData v truyn vo data
      applyData(data);
    } catch (err) {
      //thng bo li qu trnh fetch
      setError(err);
    }
    //t li Isloading l false khi qu trnh fetch xong
    setIsLoading(false);
  });

  //tr v trng thi isLoading, error, v hm sendRequest(config, applyData)
  return {
    isLoading,
    error,
    sendRequest,
  };
};

export const getHeaders = () => {
  const token = localStorage.getItem("token");

  return {
    "Content-Type": "application/json",
    Authorization: token ? "Bearer " + token : "",
  };
};

export const saveUserInfo = (data) => {
  return window.localStorage.setItem("user_info", JSON.stringify(data));
};

export const getUserInfo = () => {
  const data = window.localStorage.getItem("user_info");

  return data ? JSON.parse(data) : null;
};
export const saveTableInfo = (data) => {
  return window.localStorage.setItem("TABLEID", JSON.stringify(data));
};

export const getTableInfo = () => {
  const data = window.localStorage.getItem("TABLEID");

  return data ? JSON.parse(data) : null;
};

export default useHttp;
